// src/components/dashboard/SidebarItem.tsx
'use client';
import React from 'react';
import Link from 'next/link';
import { useRouter, usePathname } from 'next/navigation';

interface SidebarItemProps {
  icon: React.ReactNode;
  text: string;
  path: string;
}

export const SidebarItem: React.FC<SidebarItemProps> = ({ icon, text, path }) => {
  const pathname = usePathname();

  return (
    <Link 
      href={path}
      className={`flex gap-4 items-center px-4 py-2 rounded-lg cursor-pointer transition-colors duration-200
        ${pathname === path ? 'bg-gray-100 font-medium' : 'hover:bg-gray-50'}`}
    >
      <div className="w-6 h-6 flex items-center justify-center">
        {icon}
      </div>
      <span className="text-base">{text}</span>
    </Link>
  );
};